/* src/components/Merchandise.css */
.merchandise-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .merchandise-container h1 {
    text-align: center;
    margin-bottom: 30px;
    color: #fff;
  }
  
  .merchandise-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
  }
  
  .merchandise-item {
    background: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    overflow: hidden;
    transition: transform 0.3s ease;
  }
  
  .merchandise-item:hover {
    transform: scale(1.05);
  }
  
  .merchandise-item a {
    text-decoration: none;
    color: #fff;
  }
  
  .merchandise-item img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .merchandise-item p {
    padding: 10px;
    text-align: center;
    font-weight: bold;
  }
  
  @media (max-width: 768px) {
    .merchandise-grid {
      grid-template-columns: 1fr;
    }
  }