/* src/components/Home.css */
.home-container {
    padding: 20px;
    background-color: #1a1a1a;
    color: #fff;
    text-align: center;
}

.hero-section {
    position: relative;
    text-align: center;
    color: white;
    margin-bottom: 20px;
    height: 50vh; /* Set a fixed height for the hero section */
}

.hero-image {
    width: 100%;
    height: 100%; /* Make the image fill the hero section */
    object-fit: cover;
    opacity: 0.8;
}

.hero-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.7);
    padding: 20px;
    border-radius: 10px;
    width: 90%;
    max-width: 600px;
    box-sizing: border-box;
    z-index: 1;
}

.content-section {
    margin-top: 20px; /* Reduce the top margin */
    padding: 20px;
}

.content-section h2 {
    margin-bottom: 20px;
}

.content-section p, .content-section ul {
    margin-bottom: 20px;
    font-size: 18px;
}

.content-section ul {
    list-style-type: none;
    padding: 0;
}

.content-section ul li {
    background: #333;
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
    .hero-section {
        height: 40vh; /* Reduce the height on smaller screens */
    }

    .hero-text {
        font-size: 14px; /* Reduce font size on smaller screens */
        padding: 10px;
    }

    .hero-text h1 {
        font-size: 24px; /* Adjust the heading size */
    }

    .content-section h2 {
        font-size: 20px; /* Adjust subheading size */
    }

    .content-section p, .content-section ul {
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    .hero-text {
        width: 95%;
        padding: 10px;
        font-size: 14px;
    }

    .content-section p, .content-section ul {
        font-size: 14px;
    }
}
