/* src/components/CornerstoneFestival.css */
.cornerstone-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    color: #fff;
}

.cornerstone-container h1 {
    text-align: center;
    margin-bottom: 30px;
}

.festival-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.poster-container {
    width: 100%;
    max-width: 600px;
    margin-bottom: 40px;
}

.festival-poster {
    width: 100%;
    height: auto;
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
    border-radius: 10px;
}

.festival-info {
    width: 100%;
    max-width: 800px;
}

.festival-info h2 {
    margin-top: 30px;
    margin-bottom: 15px;
    color: #ddd;
}

.festival-info p {
    font-size: 18px;
    line-height: 1.6;
    margin-bottom: 20px;
}

.festival-info ul {
    list-style-type: none;
    padding: 0;
}

.festival-info ul li {
    font-size: 18px;
    margin-bottom: 10px;
}

.festival-info a {
    color: #4CAF50; /* Change this to match your color scheme */
    text-decoration: none;
    transition: color 0.3s ease;
}

.festival-info a:hover {
    color: #45a049; /* Slightly darker shade for hover effect */
    text-decoration: underline;
}

@media (max-width: 768px) {
    .festival-content {
        flex-direction: column;
    }
    
    .poster-container {
        margin-right: 0;
        margin-bottom: 30px;
    }
}